<!--    <button @click="신고수[i]++">허위매물신고</button> <span>신고수 : {{ 신고수[i] }}</span>-->
<!--  </div>-->

  <Transition name="fade">
    <Modal @closeModal="모달창열렸니 = false" :원룸들="원룸들" :누른거="누른거"
           :모달창열렸니="모달창열렸니"></Modal>
  </Transition>

  <div class="menu">
    <a v-for="(a, i) in 메뉴들" :key="i"> {{ a }} </a>
  </div>

  <Discount v-if="showDiscount == true" :discountNum="discountNum"></Discount>

  <button @click="priceSort">가격낮은순정렬</button>
  <button @click="priceReverseSort">가격높은순정렬</button>
  <button @click="nameSort">상품명 가나다순정렬</button>
  <button @click="sortBack">되돌리기</button>

  <Card @openModal="모달창열렸니 = true; 누른거 = i" :원룸="원룸들[i]"
        v-for="(작명,i) in 원룸들" :key="작명"></Card>

</template>


<script>

import data from './assets/oneroom';
import Discount from "./Discount.vue";
import Modal from "./Modal.vue";
import Card from "./Card.vue";

export default {
  name: 'App',
  data() {
    return {
      discountNum : 30,
      showDiscount : true,
      원룸들오리지널 : [...data],
      누른거 : 0,
      원룸들 : data,
      모달창열렸니 : false,
      신고수 : [0,0,0],
      메뉴들 : ['Home', 'Shop', 'About'],
      products : ['역삼동원룸', '천호동원룸', '마포구원룸'],
      price : [70, 50, 30],
      블루 : 'color : blue'
    }
  },
  methods : {
    priceSort() {
      this.원룸들.sort(function (a,b) {
        return a.price-b.price
      })
    },
    sortBack() {
      this.원룸들 = [...this.원룸들오리지널];
    },
    priceReverseSort() {
      this.원룸들.sort(function (a,b) {
        return b.price-a.price;
      })
    },
    nameSort() {
      this.원룸들.sort( (a,b) => a.title.localeCompare(b.title))
    }
  },
  mounted() {
    setInterval(() => {
      this.discountNum -= 1;
      if (this.discountNum <= 0) {
        this.showDiscount = false;
      }
    }, 1000)
  },
  components: {
    Discount : Discount,
    Modal : Modal,
    Card : Card,
  },
}
</script>

