<template>
  <div class="black-bg" v-if="모달창열렸니 === true">
    <div class="white-bg">
      <img :src="원룸들[누른거].image" alt="방사진" class="room-img">
      <h4>{{ 원룸들[누른거].title }}</h4>
      <p>{{ 원룸들[누른거].content }}</p>
      <input v-model="month">
      <p>{{ month }}개월 선택함 : {{ 원룸들[누른거].price*month }} 원</p>
      <button @click="$emit('closeModal')">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Modal-Modal',
  data() {
    return {
      month : 1,
    }
  },
  watch : {
    month(a) {
      if (isNaN(a) === true) {
        alert('숫자만 입력하쇼!');
        this.month = 1;
      }
      else if (a == 2) {
        alert('2개월만 사는것은 용납할 수 없어요!');
        this.month = 1;
      }
    },
  },
  props : {
    원룸들 : Object,
    누른거 : Number,
    모달창열렸니 : Boolean,
  }
}
</script>

